.nav-bar {
  background-color: $clr1;
  height: 194px;
  width: 100%;
}

.nav-bar-subcontainer {
  height: 100%;
  align-items: center;
}

.logo-container {
  width: 160px;
  height: 40px;
  margin-right: 80px;

  @include for-mobile-layout {
      width: 128px;
      height: 32px;
      margin-right: 0;
  }
}

.logo-menu-container {
    @include for-mobile-layout {
        width: 100%;
        align-items: center;
        justify-content: space-between;
    }
}

.menu-btn {
  display: none;

  @include for-mobile-layout  {
    display: flex;
    height: 17px;
    width: 20px;
  }
}

.home-button {
  margin-right: 43px;
}

.full-menu {
  display: flex;
  flex: 1;
  justify-content: space-between;

  @include for-mobile-layout  {
    display: none;
  }
}
