.testimonials {
  background-color: $clr1;
  height: 745px;
  width: 100%;
}

.testimonials-title {
  margin-top: 140px;
  text-align: center;
  font-family: "livvic-bold";
  font-size: rem(48px);
  align-self: center;
  line-height: 48px;
  margin-bottom: 63px;
}

.text-emphasis {
  color: #79c8c7;
}

.card-container {
  max-width: 350px;
}

.card-subcontainer {
  position: relative;
  top: -21px;
}

.quotes-img-container {
  width: 67px;
  height: 56px;
}

.card-text {
  width: 100%;
  height: 97px;
  font-family: "livvic-semi";
  font-size: rem(15px);
  line-height: 25px;
  text-align: center;
  margin-bottom: 27px;
}

.card-person-name {
  width: 100%;
  height: 28px;
  color: $clr3;
  font-family: "livvic-bold";
  font-size: 18px;
  line-height: 28px;
  text-align: center;
  margin-bottom: 2px;
}

.card-person-job-title {
  width: 100%;
  height: 18px;
  font-family: "livvic-med-ita";
  font-size: rem(13px);
  line-height: 18px;
  text-align: center;
  margin-bottom: 32px;
}

.card-person-img-container {
  height: 62px;
  width: 62px;
}
